import React, { Component } from "react";
import Card from "react-md/lib/Cards/Card";
import CardText from "react-md/lib/Cards/CardText";
import Helmet from "react-helmet";
import Button from "react-md/lib/Buttons";
import { Link } from "gatsby";
import { TextField, Chip } from "react-md";
import Layout from "../layout";
import config from "../../data/SiteConfig";

class Search extends Component {
  render() {
    return (
      <Layout title="모델검색">
        <div className="about-container">
          <Helmet>
            <title>{`search | ${config.siteTitle}`}</title>
            <link rel="canonical" href={`${config.siteUrl}/search/`} />
          </Helmet>
          <div className="about-container md-grid mobile-fix">
            <Card className="md-grid md-cell--8">
              <div>
                <CardText>
                  <table height="40px" />
                  <h2 className="md-title-3" align='left'>
                    {" "}
                    3D모델 검색{" "}
                  </h2>
                  <table height="40px" />
                  <h4 className="md-title-3" align='left'>
                    나에게 필요한 모델을 검색하세요!
                  </h4>
                  <table height="40px" />
                  <div className="md-grid">
                    <table align="center">
                      <colgroup>
                        <col width="90%" />
                        <col width="10%" />
                      </colgroup>
                      <tr>
                        <td>
                          <TextField
                            className="search_bar"
                            id="floating-center-title"
                            label="키워드 입력"
                            lineDirection="right"
                            placeholder="  "
                            width="20px"
                          />
                        </td>
                        <td width="80px">
                          <Link to={"/tags/car"}>
                            <Button flat primary iconClassName="fa fa-search" />
                          </Link>
                        </td>
                      </tr>
                    </table>
                  </div>
                  <Link to={"/tags/장난감"}>
                    <Chip label="장난감" />
                  </Link>
                  <Link to={"/tags/미니어쳐"}>
                    <Chip label="미니어쳐" />
                  </Link>
                  <Link to={"/tags/공학적"}>
                    <Chip label="공학적" />
                  </Link>
                  <Link to={"/tags/예술적"}>
                    <Chip label="예술적" />
                  </Link>
                  <Link to={"/tags/기계부품"}>
                    <Chip label="기계부품" />
                  </Link>
                  <Link to={"/tags/자동차부속품"}>
                    <Chip label="자동차부속품" />
                  </Link>
                  <Link to={"/tags/실용"}>
                    <Chip label="실용" />
                  </Link>
                  <Link to={"/tags/전자공학"}>
                    <Chip label="전자공학" />
                  </Link>
                  <Link to={"/tags/조각상"}>
                    <Chip label="조각상" />
                  </Link>
                  <Link to={"/tags/동물"}>
                    <Chip label="동물" />
                  </Link>
                </CardText>
              </div>
              <table height="400px" />
            </Card>
          </div>
        </div>
      </Layout>
    );
  }
}

export default Search;
